.about {
  margin: 4rem 0;
}

.about_container {
  gap: 4rem;
}

.about--item .title-container h2 {
  color: #14142B;
  letter-spacing: 1px;
  font-size: 24px;
  line-height: 34px;
  font-weight: 700;
}

.about--item .text-container {
  margin: 3rem 0;
}

.about--item .text-container p {
  color: #646464;
  font-weight: 400;
  font-size: 18px;
  line-height: 32px;
  letter-spacing: 0.75px;
}

.about--item .button-container .button {
  display: inline-block;
  padding: 9px 12px;
  font-weight: 600;
  font-size: 13px;
  line-height: 22px;
  letter-spacing: 0.25px;
}

.about--item .button-container .button svg {
  display: inline-block;
  margin-left: 0.1rem;
  transition: transform 0.5s ease;
  height: 13px;
  transform: translate(0px, 2px);
}

.about--item .button-container .button:hover svg {
  transform: translate(0.5rem, 2px);
}

@media (min-width: 992px) {
  .about_container {
    gap: 6rem;
  }
  .about--item .title-container h2 {
    font-size: 48px;
    line-height: 56px;
  }
}